@media (min-width: 767px) /* @grid-float-breakpoint -1 */
{
    .navbar-fixed-top
    {
    position: absolute;
    top: auto;
    }
}
.navbar-custom .navbar-brand{
	color:#fff;
}
header{
	box-shadow: inset 0 0 0 1000px rgba(26, 0, 45, 0.59);
}
#rendering-web{
	background-color:#5b18ba;
	color:#fff;
}

.intro-text .section-subheading{
	width: 65%;
    margin: auto;
    padding: 20px 0;
    font-size:18px;
}
.intro-lead-in{
	margin-bottom:0;
}
.btn-learn-more{
	background-color:#222222;
}
#rendering-web .btn-learn-more{
	border : 1px solid #fff;
	color:#fff;
	background:transparent;
}

btn:active, .btn:hover{
	background-image: none;
    outline: 0;
    -webkit-box-shadow: inset 0 3px 5px rgba(0, 0, 0, .5);
    box-shadow: inset 0 3px 5px rgba(0, 0, 0, .5);
}

/*
header .btn-learn-more:hover, .btn-xl:hover, header .btn-learn-more.focus{
	border : 1px solid #5b18ba;
	background-color:#5b18ba;
	color:#fff;
}
*/
header .btn-learn-more{
	border : 1px solid #5b18ba;
}

.btn-learn-more:hover{
	color:#fff;
}

#services{
	background-color:#333333;	
} 
#services h2{
	color:#fff;
}
#services i{
	color:#5b18ba;
}
#services .service-heading{
	color:#fff;
}

#team h3{
	color:#5b18ba;
}
#team h4{
	color:#8d08a2;
}
.team-member img{
	width: 200px;
    height: 200px;
}
.clients{
	background-color:#5b18ba;
}
.list-inline li a img{
	width:100px;

}
.clients .list-inline li{
	vertical-align: middle;
    padding: 0 50px;	
}

#contact .service-heading, footer .service-heading{
	color:#fff;
}
#contact .social-buttons li a{
	text-align:center;
}

#contact .text-muted , #services .text-muted{
	color: #ccc;
}
#contact input, #contact textarea{
	color:#ccc;
	font-weight: bold;
}
.btn-submit, .btn-submit:hover, .btn-submit:focus{
	border : 1px solid #5b18ba;
	background-color:#5b18ba;
	color:#fff;
}
#contact .btn-contact {
	background:transparent; 
	border:2px solid #7b7b7b; 
	color:#7b7b7b; 
	text-align:center; 
	font-family:"Roboto-Bold"; 
	font-size:26px; padding:10px 20px; 
	border-radius:10px; 
	-webkit-border-radius:10px; 
	-moz-border-radius:10px; 
	-o-border-radius:10px; 
	-ms-border-radius:10px;
}
#contact .btn-contact:hover {
	background:#fff; 
	border:2px solid #fff; 
	color:#5b18ba;
}
#team .text-muted, h3 .text-muted{
	color:#333;
}
.section-heading, h3,h2{
	text-transform:none;
	font-weight: normal;
}
.section-subheading{
	font-family: inherit;
    font-style: normal;
    line-height:1.5;
}
.intro-lead-in{
	font-style:normal;
}
footer{
	background-color:#1f1f1f;
}
.border-top{
	border-top:1px solid #333;
}
.portfolio-modal{
	color:#333;
}

::-webkit-input-placeholder {
	color: #999 !important;
	text-transform:none !important;
}
 
:-moz-placeholder { /* Firefox 18- */
	color: #999 !important;  
	text-transform:none !important; 
}
 
::-moz-placeholder {  /* Firefox 19+ */
	color: #999 !important;  
	text-transform:none !important; 
}
 
:-ms-input-placeholder {  
	color: #999 !important;  
	text-transform:none !important; 
}
#contact ul.social-buttons li a{
	border:1px solid #ccc;
	background:#ccc;
	color:#222;
}
#contact ul.social-buttons li a:hover{
	background-color:#fff;
	color:#5b18ba;
}
.service-icon{
	font-size:64px
}
.service-icon:hover{ 
	color:#fff !important;
}
.social-buttons, .service-icon,img.img-responsive.img-circle:hover{ 
	-webkit-transition: all .2s linear; 
	-o-transition: all .2s linear; 
	-moz-transition: all .2s linear; 
	transition: all .2s linear; 
}
img.img-responsive.img-circle:hover {
    border-color: #5b18ba;
}

/* Popup CSS */
.the-price h1 {
	margin:10px 0;
}
.the-price small {
	font-size:16px; 
	color:#333; 
	margin-bottom:10px; 
	display:inline-block;
}
.btn-signup {
	background:#5c1bb7; 
	border:1px solid #5c1bb7; 
	color:#fff; padding:8px 16px; 
	font-size:18px; 
	display:inline-block; 
	border-radius:4px;
}
.btn-signup:hover {
	color:#333; 
	border:1px solid #5c1bb7; 
	background:transparent; 
	text-decoration:none;
}
.portfolio-modal .table {
	margin:0;
}
.portfolio-modal .panel-body {
	padding:0;
}
.portfolio-modal .table-striped > tbody > tr:nth-of-type(odd) {
	background-color:#ddd;
}
.portfolio-modal .panel-footer {
	background:transparent; 
	border-top:1px solid #ddd; 
	color:#333;
}
@media only screen and (max-device-width : 768px) {
	#contact ul.social-buttons li {margin-bottom:10px;}
}


@media only screen and (min-device-width : 320px) and (max-device-width : 767px) {
	#rendering-web {text-align:center;}
	#rendering-web h2 {text-align:center;}
	#contact p.text-muted {text-align:left;}
	.submit-contact-button{text-align:center;}
	.render-web-img{display: inline;max-width: 70%;}
}
@media only screen and (max-device-width : 320px) {
	.clients .list-inline li {padding:0 50px;}
	#contact p.text-muted {text-align:left;}
	.submit-contact-button{text-align:center;}
	.render-web-img{display: inline;max-width: 70%;}
}














